/* Dashboard Component Styles */

.dashboard {
  text-align: center;

  border-width: 5px;
  border-color:#e8aa24
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.grid-pad {
  padding-top: 20px;
}

.col-1-4 {
  flex-basis: 24%;
  margin: 0.5%;
  text-align: center;
}

.module {
  padding: 10px;
  border-radius: 10px;
  color: #e4ff1a;
  background-color: #ff5724;
  transition: background-color 0.3s;
}

.module:hover {
  background-color: #e8aa24;
}

.module h4 {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .col-1-4 {
    flex-basis: 48%;
    margin: 1%;
  }
}

@media (max-width: 300px) {
  .col-1-4 {
    flex-basis: 98%;
    margin: 1%;
  }
}

